import React, { Component} from 'react';
import Post,{fixOrg} from '../../../../components/post';
import {Container} from 'reactstrap';
import Link from 'next/link'
import Layout from '../../../../components/layout'
import OrgViewer from 'react-orgviewer'

export class Title extends React.Component {
    render() {
	return <h4>Why and how to ditch AWS for cost reduction</h4> 
    }
}
export class PreviewContents extends React.Component {
    render() {
	return <p>AWS is an extremely evolved and useful ecosystem that makes many things easy. Nevertheless, there comes a time to say goodbye.</p>
    }
}


export default class extends Post {
    preview() {
	return (<Container>
		<Title/>
		<PreviewContents/>
		<Link href={this.state.path}>Find out.</Link>
		</Container>)

    }
    post() {
	return (<Layout {...this.props} navmenu={false} title={Title.text}>
		<Title/>
		<PreviewContents/>
		<OrgViewer source={fixOrg(require('./article.org'))} />

		</Layout>)

    }
}



